.board {
    display: flex;
    flex-direction: column;
    width: 400px;
    height: 400px;
}

.row {
    display: flex;
}

.square {
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    font-weight: bold;
    cursor: pointer;
    box-sizing: border-box; /* 추가된 부분 */
    transition: background-color 0.3s ease; /* 배경색 전환을 부드럽게 하기 위해 추가 */
}

.white {
    background-color: #f0d9b5;
}

.black {
    background-color: #b58863;
}

/* 백색 기물 스타일 */
.white-piece {
    color: white;
}

/* 흑색 기물 스타일 */
.black-piece {
    color: black;
}

.piece {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

/* 마지막 이동된 위치 강조 */
.last-move {
    background-color: #a2d5f2 !important; /* 마지막 이동된 위치를 부드러운 파란색으로 강조 */
}

.selected {
    border: 2px solid red; /* 선택된 칸 강조 표시 */
    box-sizing: border-box; /* 추가된 부분 */
}

.checkmate-message {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 48px;
    font-weight: bold;
    color: red;
    background-color: rgba(255, 255, 255, 0.8);
    padding: 20px;
    border-radius: 10px;
    z-index: 1000; /* 화면 위에 표시되도록 z-index 설정 */
    text-align: center;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.game-over-message {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 48px;
    font-weight: bold;
    color: red;
    background-color: rgba(255, 255, 255, 0.8);
    padding: 20px;
    border-radius: 10px;
    z-index: 1000; /* 화면 위에 표시되도록 z-index 설정 */
    text-align: center;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.check-message {
    position: absolute;
    top: 10%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 36px;
    font-weight: bold;
    color: blue;
    background-color: rgba(255, 255, 255, 0.8);
    padding: 10px;
    border-radius: 10px;
    z-index: 1000; /* 화면 위에 표시되도록 z-index 설정 */
    text-align: center;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.difficulty-selection {
    text-align: center;
    padding: 20px;
}

.start-message {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 48px;
    font-weight: bold;
    color: green;
    background-color: rgba(255, 255, 255, 0.9);
    padding: 20px;
    border-radius: 10px;
    z-index: 1000;
    text-align: center;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}
