.difficulty-selection {
    text-align: center;
    padding: 20px;
    max-width: 600px;
    margin: 0 auto;
    font-family: Arial, sans-serif;
}

.difficulty-selection h1 {
    font-size: 36px;
    margin-bottom: 20px;
    color: #333;
}

.difficulty-selection p {
    font-size: 18px;
    margin-bottom: 20px;
    color: #555;
}

.difficulty-selection h2 {
    font-size: 24px;
    margin-bottom: 10px;
    color: #444;
}

.difficulty-selection input[type="range"] {
    width: 80%;
    margin: 10px 0;
    cursor: pointer;
}

.difficulty-selection button {
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    transition: background-color 0.3s ease;
}

.difficulty-selection button:hover {
    background-color: #45a049;
}
